import { PageHeading } from "../components/aboutuspageblocks";
import HeaderBar from "../components/header";
import { Clientsblock, Partnershipblock } from "../components/Homepageblocks";

export const Aboutuspage = (props) => {
    return(
        <>
        <HeaderBar  menuitems={["Home", "Contact Us", "About Us", "Products"]} activeitem={0}/>
        <PageHeading />
        <Partnershipblock />
        <Clientsblock />
        </>
    );
}

export default Aboutuspage;